import axios from 'axios'
import * as ajaxUrl from '../ajax.config'
var mockAddress = {}, mockArea = {};
axios.get(ajaxUrl.bespeak).then((res) => {
  // console.log(JSON.parse(res.data.resData.areaJson))
  var mockArr = JSON.parse(res.data.resData.areaJson)
  mockArr.forEach(val => {
    mockAddress[val.label] = val.children.map(labelVal);
    val.children.forEach(child => {
      if(child.hasOwnProperty('children')){
        mockArea[child.label] = child.children.map(labelVal);
      }else{
        mockArea[child.label] = ['']
      }
    })
  })
  // console.log(JSON.stringify(mockAddress))
})
function labelVal(item) {
  return item.label
}
export const address2 = mockAddress
